
const Precio = (props) => {

     const {price, percent_change_1h, percent_change_7d, percent_change_24h} = props.precio;
     return (

         <div className=" card text-white bg-info mb-3">
              <div className="card-header">Last Information </div>

              <div className=" card-body">
                   <h4 className="card-title"> Actual Price U$D : {price.toFixed()}</h4>
              </div>
              < div className = " d-md-flex justify-content-between m-2" >
                   <p className="card-text"> Last Hour {' '}<span className="font-weight-bold">{percent_change_1h}</span> %</p>
                   <p className="card-text">Last 24 Hs  {' '}<span className="font-weight-bold">{percent_change_24h}</span> %</p>
                   <p className="card-text">Last 7 Days   {' '}<span className="font-weight-bold">{percent_change_7d}</span> %</p>
              </div>

         </div>
     );


};


export default Precio;